.item__toggle {
  position: absolute;
  top: 2px;
  right: 2px;
  cursor: pointer;
  border: 1px solid #b9bec1;
  border-radius: 4px;
  background: #ced8dd none repeat scroll 0% 0%;
  display: inline-block;
  height: 18px;
  font-size: 12px;
  color: #716e6e;
  transition: all 0.3s 0.1s;
  padding: 0 10px 0 20px; }
  .item__toggle::before {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    background-color: #fff;
    height: 16px;
    width: 16px;
    border: 1px solid #acacae;
    border-radius: 4px;
    transition: all 0.3s; }
  .item__toggle span {
    position: relative;
    top: 1px; }

.item__input:checked + .item__toggle::before {
  left: calc(100% - 16px); }

.item__input:checked + .item__toggle {
  background: #0084d1 none repeat scroll 0% 0%;
  border-color: #4b91ba;
  color: #edecec;
  padding: 0 20px 0 10px; }

