.App {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap; }

.error-message {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%; }

.steps {
  width: 772px;
  height: 539px;
  display: flex;
  background-color: antiquewhite;
  border: 1px solid #c9d1d5; }

.steps__inner {
  width: 100%; }

.step {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%; }

.step_main {
  justify-content: center; }

.step_last .content {
  overflow: auto; }

.content {
  flex: 1;
  padding: 15px;
  background-color: antiquewhite; }

.content__inner {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -25px; }

.result {
  padding: 0 5px;
  text-align: left;
  font-size: 12px;
  line-height: 1.4;
  overflow-y: scroll;
  height: 586px;
  width: 450px;
  background-color: #fbfbfb;
  border: 1px solid #dedede;
  margin: 15px 15px 15px 0; }
  .result h2 {
    margin: 0; }



