.hd {
  display: flex;
  align-items: center;
  background-color: #f5f6f8;
  border-bottom: 1px solid #c9d1d5;
  height: 64px;
  flex: 0 0 auto; }

.hd__item {
  padding: 0 40px 0 10px;
  font-weight: 700;
  transition: all 0.3s;
  font-size: 12px;
  color: #c3ccd3;
  line-height: 20px;
  text-transform: uppercase;
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  flex: 1;
  cursor: default; }
  .hd__item::after {
    content: "";
    background-color: #f5f6f8;
    background-image: url(../img/icons/array-step.png);
    background-repeat: no-repeat;
    background-position: -54px 0;
    width: 27px;
    height: 62px;
    position: absolute;
    top: 0;
    right: 0;
    float: right; }
  .hd__item.active {
    background-color: #dbe3e6;
    color: #40484e; }
    .hd__item.active:after {
      background-position: 0 0; }
  .hd__item.active-prev {
    background-color: #dbe3e6;
    color: #40484e; }
    .hd__item.active-prev:after {
      background-position: -81px 0; }

.hd__title {
  display: flex;
  align-items: center;
  background-color: #dbe3e6;
  border-right: 1px solid #c9d1d5;
  padding: 0 10px;
  height: 100%;
  width: 200px;
  cursor: default; }
  .hd__title:hover {
    background-color: #dbe3e6; }

.hd__ico {
  margin-right: 15px;
  width: 114px;
  height: 100%;
  display: flex;
  align-items: center; }

.hd__name {
  font-weight: 700;
  font-size: 12px; }

.hd__item_step-4 {
  text-indent: -999999px;
  flex: 0 0 auto;
  padding: 0;
  width: 70px;
  display: flex;
  justify-content: center; }
  .hd__item_step-4:before {
    content: '';
    background: url(../img/icons/config-finish.png) no-repeat center;
    width: 23px;
    height: 28px;
    display: inline-block; }
  .hd__item_step-4::after {
    background-image: none;
    width: 3px; }
  .hd__item_step-4.active::after {
    background-image: none;
    width: 3px;
    background-color: #0079c8; }



