input[type='text'],
textarea {
  width: 100%;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
  box-shadow: inset 0 0 1px 1px #f1f1f1;
  padding: 12px 12px 11px;
  color: #6d6c6c;
  font-size: 12px;
  transition: all 0.3s; }
  input[type='text']:focus,
  textarea:focus {
    box-shadow: 0 0 0 1px #81b8ed;
    border-color: #81b8ed;
    outline: none; }
  input[type='text'].error,
  textarea.error {
    box-shadow: 0 0 0 1px red;
    border-color: red; }



